<template>
  <div>
    <img ref="cameraImg" :src="imageSource" alt />
  </div>
</template>

<script>
import imageCaptureStore from '@/store/imageCapture.js'

export default {
  name: 'upload',
  data: () => ({
    imgSrc: null
  }),
  computed: {
    imageSource() {
      return `data:image/jpeg;base64,${imageCaptureStore.capturedImage}`
    }
  },
  methods: {
  },
  mounted() {
  }
}
</script>
